import React from "react";
import "./Footer.css";

const Footer = () => {
  return (
    <footer className="footer">
      <div className="footer-container">
        <p>&copy; {new Date().getFullYear()} Vtech Ventures. All rights reserved.</p>
        <p>
          Made with <span className="heart"></span> by your team
        </p>
      </div>
    </footer>
  );
};

export default Footer;